import React, { useEffect, useState } from "react";
import "./Fullstack.css"
import Spline from "@splinetool/react-spline";
import MyLoader from "../../Common/MyLoader/MyLoader";

import data from "../../Assets/fullstack";
import { Col, Row } from "react-bootstrap";
import { Link } from "react-router-dom";

const Fullstack = () => {
  const [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false);
    }, 2000);
  }, []);
  return (
    <Row className="py-5">
      <h2 className="centered job py-5 z-1">
        Web Development
      </h2>
      <Col sm={12} md={6} className="spline-container">
        {isLoading ? (
          <MyLoader />
        ) : (
          <div className="simple-list-example-scrollspy centered">
            <div className={`spline ${isLoading ? "d-none" : ""}`}>
              <Spline scene="https://prod.spline.design/7hQVzQV-tjbJ4NMR/scene.splinecode" />
            </div>
          </div>
        )}
      </Col>
      <Col sm={12} md={6}>
        <div className="centered flex-column gap-5 z-0">
          {data.map((datas) => (
            <div
              style={{ background: `url(${datas.path})` }}
              key={datas.id}
              className="box entering fullstack-container"
            >
              <div className="fullstack-bg p-5">
                <p>{datas.title}</p>
                <p>{datas.description}</p>
                <p className="text-small text-danger">{datas.note}</p>
                <Link target="_blank" to={datas.link}>
                  Visit Now
                </Link>
                </div>
            </div>
          ))}
        </div>
      </Col>
    </Row>
  );
};

export default Fullstack;
